.button {
  overflow: hidden;
  position: relative;
}

.ripple-container {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  overflow: hidden;
  position: absolute;
}

.ripple-container span {
  position: absolute;
  top: ...;
  left: ...;
  height: ...;
  width: ...;
  transform: scale(0);
  border-radius: 100%;
  opacity: 0.25;
  background-color: #fff;
  animation-name: ripple;
  animation-duration: 850ms;
}

@keyframes ripple {
  to {
    opacity: 0;
    transform: scale(2);
  }
}